import {Component} from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'UpSlack';
  public rawMinutes;
  public hours;
  public minutes;
  public fee;
  public rate;
  public exchangeRate;
  private trackers = {
    'tracker-1': {},
    'tracker-2': {},
  };

  constructor() {
    ['tracker-1', 'tracker-2'].map(tracker => {
      ['rawMinutes', 'rate', 'exchangeRate', 'fee'].map(field => {
        let defaultValues = {'rawMinutes':0, 'rate':57, 'exchangeRate':57.66, 'fee':20};
        let val;
        if (val = parseFloat(localStorage.getItem(tracker+'-'+field))) {
          this.trackers[tracker][field] = val;
        } else {
          this.trackers[tracker][field] = defaultValues[field];
        }
        console.log(tracker, field);
      });
    });

    this.changeTime(1,0);
    this.changeTime(2,0);
  }

  changeTime(tracker: number, diff: number) {
    if (0 > this.trackers['tracker-'+tracker]['rawMinutes'] + diff) {
      return;
    }
    this.trackers['tracker-'+tracker]['rawMinutes'] += diff;
    this.trackers['tracker-'+tracker]['hours'] = Math.floor(this.trackers['tracker-'+tracker]['rawMinutes'] / 60);
    this.trackers['tracker-'+tracker]['minutes'] = this.trackers['tracker-'+tracker]['rawMinutes'] % 60;

    this.updateLS();
  }

  private updateLS() {
    ['tracker-1', 'tracker-2'].map(tracker => {
      ['rawMinutes', 'rate', 'exchangeRate', 'fee'].map(field => {
        localStorage.setItem(tracker+'-'+field, this.trackers[tracker][field]);
      });
    });
  }
}



